import { h, Component } from 'preact';
import Api from '../../api';
import PractitionerPortrait from './PractitionerPortrait';

export default class Practitioner extends Component {

	getPractitioners = async (props) => {
		Promise.all(
			[
				Api.getPractitionersForTreatment(props.selected.treatment.id),
				Api.getPractitionersForLocation(props.selected.location.id),
			])
			.then(([pracsForTreatment, pracsForLocation]) => {
				let longer = pracsForTreatment;
				let shorter = pracsForLocation;
				if (pracsForLocation.size > pracsForTreatment.size) {
					longer = pracsForLocation;
					shorter = pracsForTreatment;
				}
				const longerKeys = Array.from(longer.keys());

				const practitionerMap = new Map(
					Array.from(shorter.entries()).filter(([k, _]) => longerKeys.includes(k))
				);

				this.setState({ practitionerMap });
			});
	};

	selectPractitioner = id => {
		const practitioners = [this.state.practitionerMap.get(id)];
		this.props.updateSelections({ practitioners });
	};

	noPreference = () => {
		const practitioners = Array.from(this.state.practitionerMap.values());
		this.props.updateSelections({ practitioners });
	};

	constructor(props) {
		super(props);
		this.setState({
			practitionerMap: null,
		});
		this.noPreferencePractitioner = {
			id: '0',
			label: 'No Preference',
			description: 'We will offer you all our available appointments and you can choose the date and time that suits you the best',
		};
	}

	componentDidMount() {
		this.getPractitioners(this.props);
	}

	pracPortraits = (pracMapArray) => (
		<div className="prac-container">
			{pracMapArray.map(practitioner => <PractitionerPortrait onClick={this.selectPractitioner} practitioner={practitioner} selected={this.props.selected.practitioners[0]} />)}
		</div>
	);

	noPracs = () => (
		<div className="prac-container no-pract">
			<div className="no-practioner-container">
				<h3>No practitioner available!</h3>
				<input type="submit" value="Go Back" className="submit-button w-button" onClick={this.props.page.back} />
			</div>
		</div>
	);

	render(props, state, context) {
		const { practitioners } = props.selected;
		const prac = practitioners.length > 1 ? this.noPreferencePractitioner : practitioners[0];
		const pracMapArray = state.practitionerMap && Array.from(state.practitionerMap.values());

		return (
			<div data-w-id="9055bb6e-70a3-35f1-c7a4-1d8c302f7f35" className="booking-card">
				<div data-w-id="9055bb6e-70a3-35f1-c7a4-1d8c302f7f36" className="panel left">
					<h3 className="app-heading">Select a practitioner:</h3>
					{
						state.practitionerMap
							?
							pracMapArray.length > 0 ? this.pracPortraits(pracMapArray): this.noPracs()
							: <h4>Loading...</h4>
					}
				</div>
				<div data-w-id="9055bb6e-70a3-35f1-c7a4-1d8c302f7f49" className="panel right">
					<h4 className="app-heading">{ prac && `You have chosen: ${prac.label}`}</h4>
					<div className="info-container">
						<p>{prac && prac.description}</p>
					</div>
					<div className="form-block w-form">
						<input type="submit" value="Go Back" className="submit-button w-button" onClick={props.page.back} />
						<input type="submit" value="I don&#x27;t have a preference" className="submit-button w-button" onClick={this.noPreference} />
						<input type="submit" disabled={practitioners.length === 0} className="submit-button active w-button" onClick={props.page.next} />
					</div>
				</div>
			</div>
		);
	}
}



