import { h, Component } from 'preact';
import ReactPixel from 'react-facebook-pixel';

import PractitionerPortrait from '../practitioner/PractitionerPortrait';
import App from '../app';
import Api from '../../api';
import { formatTime } from '../../util/dateTimeUtil';

export default class Confimation extends Component {

	book = async () => {
		const { slot, practitioners, treatment, location } = this.props.selected;
		const { patient } = this.props.details;
		const resp = await Api.postNewAppointment(slot, patient.id, practitioners[0].id, treatment.id, location.id);
		ReactPixel.track('Purchase', {
			content_ids: [treatment.id, location.id],
			content_type: treatment.name,
			currency: 'GBP',
			value: '25.0',
		});
		this.props.updateSelections(App.selectedReset);
		this.props.page.next();
	};

	render(props, state, context) {
		const { selected, details } = props;
		return (
			<div className="booking-card details-screen">
				<div data-w-id="660efcb7-ea2f-9e44-4ad4-5a1875922a6a" className="panel left full-width">
					<div className="appointment-recap-container">
						<div className="appointment-main-detail-container">
							<h3 className="app-heading">Your details:</h3>
						</div>
					</div>
					<div className="appointment-recap-grid">
						<div className="appointment-info-container">
							<div className="details-portrait-container">
								<div className="prac-portrait">
									<img src="/assets/img/blank_dude.svg" alt={`${ details.firstName } ${ details.lastName }`} />
								</div>
								<div className="pract-descript-container">
									<h5 className="pract-heading">{ details.firstName } { details.lastName }</h5>
								</div>
							</div>
						</div>
						<div className="appointment-info-container">
							<h5 className="app-heading">{ details.email }</h5>
							<h5 className="app-heading">{ details.phoneNumber }</h5>
						</div>
						<div className="appointment-info-container">
							<h5 className="app-heading">{ details.message }</h5>
						</div>
					</div>
					<h3 className="app-heading">Your appointment:</h3>
					<div className="appointment-recap-grid">
						<div className="appointment-info-container">
							<div className="details-portrait-container">
								<div className="prac-portrait">
									<img src={PractitionerPortrait.getImgName(selected.practitioners[0].label)} alt={selected.practitioners[0].label} />
								</div>
								<div className="pract-descript-container">
									<h5 className="pract-heading">{ selected.practitioners[0].label }</h5>
									<h5 className="pract-heading">{ selected.practitioners[0].designation }</h5>
								</div>
							</div>
						</div>
						<div className="appointment-info-container">
							<h5 className="app-heading">{ selected.treatment.name }</h5>
							<h5 className="app-heading">{ selected.location.business_name }</h5>
						</div>
						<div className="appointment-info-container">
							<h5 className="app-heading">{ selected.slot.toLocaleDateString() }</h5>
							<h5 className="app-heading">{ formatTime(selected.slot) }</h5>
						</div>
					</div>

					<div className="w-form">
						<input type="submit" value="Go Back" className="submit-button w-button" onClick={props.page.back} />
						<input type="submit" value="Confirm" className="submit-button w-button" onClick={this.book} />
					</div>
				</div>
				<div className="panel closed" />
			</div>
		);
	}
}



