import { h, Component } from 'preact';
import Calendar from './calendar';
import PractitionerPortrait from '../practitioner/PractitionerPortrait';
import { formatDateAndTime } from '../../util/dateTimeUtil';

export default class DateAndTime extends Component {

	selectPracFromMultiple = (prac) => {
		this.setState({ selectedPrac: prac });
	};

	back = () => {
		this.props.updateSelections({ slot: null });
		this.props.page.back();
	};

	submit = () => {
		const practitioners = this.props.selected.practitioners.length !== 1 ? [this.state.selectedPrac] : [this.props.selected.practitioners[0]];
		this.props.updateSelections({ practitioners });
		this.props.page.next();
	};

	constructor(props) {
		super(props);
		this.noPreferencePractitioner = {
			label: 'No Preference',
			description: 'We will offer you all our available appointments and you can choose the date and time that suits you the best',
		};
	}

	render(props, state, context) {
		const { practitioners, treatment, location, slot } = props.selected;
		const prac = practitioners.length !== 1 ? this.state.selectedPrac || this.noPreferencePractitioner : practitioners[0];

		return (
			<div className="booking-card extra-large">
				<div data-w-id="b76ee9d4-bc96-6001-21b2-373729f1ad63" className="panel left small">
					<div className="info-container" style={{ maxHeight: '100%' }}>
						<div className="prac-portrait calendar-page">
							<img src={PractitionerPortrait.getImgName(prac.label)} alt={prac.label} />
						</div>
						<h5 className="app-info">{prac ? prac.label : '-'}</h5>
						<h5 className="app-info">{location && location.business_name}</h5>
						<h5 className="app-info">{treatment && treatment.name}</h5>
						<h5 className="app-info">{slot ? formatDateAndTime(slot) : 'Please select a time below'}</h5>
						<h5 />
						<h5 className="app-info">(Please scroll down to select a time and confirm)</h5>

						<div className="w-form">
							<input type="submit" value="Go Back" className="submit-button w-button" onClick={this.back} />
							<input type="submit" disabled={!slot} value="Confirm Date And Time" className="submit-button w-button" onClick={this.submit} />
						</div>
					</div>
				</div>
				<div data-w-id="b76ee9d4-bc96-6001-21b2-373729f1ad6c" className="panel calendar">
					<Calendar selected={props.selected} updateSelections={props.updateSelections} selectPracFromMultiple={this.selectPracFromMultiple} />
				</div>
			</div>
		);
	}
}



