import { h, Component } from 'preact';
import Api from '../../api';

export default class TreatmentLocation extends Component {

	getTreatments = async () => {
		Promise.all([Api.getTreatments(), Api.getLocations()])
			.then(([treatmentMap, locationMap]) => {
				this.setState({
					treatmentMap,
					locationMap,
					prompt: 'Please select a treatment',
				});
			});
	};

	selectTreatment = e => {
		const treatment = this.state.treatmentMap.get(e.target.value);
		this.props.updateSelections({ treatment, practitioners: [] });
		// Put location bit in here
		if (!this.props.selected.location) { // Also check if selected Location is not available now
			const location = Array.from(this.state.locationMap.values())[0];
			this.props.updateSelections({ location });
			this.updateMap(location.id);
		}
	};

	selectLocation = e => {
		const location = this.state.locationMap.get(e.target.value);
		this.props.updateSelections({ location, practitioners: [] });
		this.updateMap(location.id);
	};

	updateMap(locId) {
		this.setState({
			locHref: this.locMap[locId].href,
			locImg: this.locMap[locId].bgUrl,
		});
	}

	constructor(props) {
		super(props);
		this.state = {
			treatmentMap: null,
			locationMap: null,
			prompt: 'Loading...',
		};
		this.locMap = {
			76479: {
				href: 'https://www.google.co.uk/maps/place/The+Physio+Crew,+Exercise+4+Less/@51.4793213,-2.5369466,17z/data=!3m1!4b1!4m5!3m4!1s0x48718fda5cd92a8f:0x5f7130cfd4b12588!8m2!3d51.479318!4d-2.5347579',
				bgUrl: { backgroundImage: "url('/assets/img/maps/fishponds.png')" },
			},
			76546: {
				href: 'https://www.google.co.uk/maps/place/Harbury+Rd,+Bristol+BS9+4PL/@51.4913429,-2.6027233,17z/data=!3m1!4b1!4m5!3m4!1s0x48719201f70696d9:0xc72e98c0036a4d2f!8m2!3d51.4913396!4d-2.6005346',
				bgUrl: { backgroundImage: "url('/assets/img/maps/henlease.png')" },
			},
			76547: {
				href: 'https://www.google.co.uk/maps/place/Abbots+Leigh,+Bristol+BS8+3RA/@51.4677568,-2.6655487,17z/data=!3m1!4b1!4m5!3m4!1s0x48718d6e02353867:0x5a30fb779950a87e!8m2!3d51.4677535!4d-2.66336',
				bgUrl: { backgroundImage: "url('/assets/img/maps/abbots-leigh.png')" },
			},
			76548: {
				href: 'https://www.google.co.uk/maps/place/The+Physio+Crew,+Pottington+Business+Park/@51.0858376,-4.0801226,17z/data=!3m1!4b1!4m5!3m4!1s0x486c3ae9b3c35865:0x422fa13947374207!8m2!3d51.0858343!4d-4.0779339',
				bgUrl: { backgroundImage: "url('/assets/img/maps/barnstaple.png')" },
			},
		};
	}

	componentWillMount() {
		this.getTreatments();
	}

	componentDidMount() {
		if (this.props.selected.location) {
			this.updateMap(this.props.selected.location.id);
		}
	}

	render(props, state, context) {
		const { treatment, location } = props.selected;

		return (
			<div data-w-id="032312e0-97e4-0cdb-7d8e-2e0b38b6438f" className="booking-card">
				<div data-w-id="032312e0-97e4-0cdb-7d8e-2e0b38b64390" className="panel left">
					<h3 className="app-heading">Please select a treatment</h3>
					<div className="form-block w-form">
						<label>Treatment *</label>
						<select id="field" value={treatment && treatment.id} className="select-field w-select" onChange={this.selectTreatment}>
							<option hidden value="">{state.prompt}</option>
							{state.treatmentMap && Array.from(state.treatmentMap.values()).map((treat) => <option key={treat.id} value={treat.id}>{treat.name}</option>) }
						</select>
						<label>Location *</label>
						<select id="field-2" value={location && location.id} className="select-field w-select" onChange={this.selectLocation}>
							<option hidden value="">{state.prompt}</option>
							{ // Move this to run when new treatment is selected (And move the one above to be set on initial get)
								state.locationMap && treatment &&
								Array.from(state.locationMap.values()).map(location => {
									if (location.appointment_type_ids.includes(parseInt(treatment.id, 10))) {
										return <option key={location.id} value={location.id}>{location.business_name} ({location.post_code})</option>;
									}
								})
							}
						</select>
						<a href={this.state.locHref} target="_blank" rel="noopener noreferrer">
							<div className="maps-location" style={this.state.locImg} />
						</a>
					</div>
				</div>
				<div data-w-id="032312e0-97e4-0cdb-7d8e-2e0b38b643a3" className="panel right">
					<h4 className="app-heading">{treatment && `You have chosen: ${ treatment.name }`}</h4>
					<div className="info-container">
						<p>{treatment && treatment.description}</p>
					</div>
					<div className="select-treatment-button-container w-form">
						<input type="submit" value="Confirm Selection" disabled={!(treatment && location)} className="submit-button w-button" onClick={props.page.next} />
					</div>
				</div>
			</div>
		);
	}
}



