import { h, Component } from 'preact';
import Api from '../../api';
import moment from 'moment';
import { formatTime, monthNumberToName } from '../../util/dateTimeUtil';

export default class Calendar extends Component {

	fetchNextXWeeks = async (x=5) => {
		const at = this.state.availableTimes;
		const timePreLoop = this.fetchMoment.toDate().getTime();

		for (const prac of this.props.selected.practitioners) {
			const pracId = prac.id;
			this.fetchMoment = moment(timePreLoop);

			let weeks = [];
			Array.from(Array(x).keys()).forEach(() => {
				weeks = [...weeks, this.getWeek(this.fetchMoment.toDate(), pracId)];
				this.fetchMoment.add(7, 'days');
			});

			const myCalendarArray = await Promise.all(weeks);
			at[pracId] = this.mergeCalArray(myCalendarArray, pracId);
		}

		this.setState(
			{ availableTimes: at },
		);
	};

	daysInMonth = (year, month) => {
		const dayCount = new Date(year, month + 1, 0).getDate();
		return Array.from(Array(dayCount).keys());
	};

	calculateStartingOffset = (year, month) => new Date(year, month, 1).getDay();

	getDates = async () => {
		this.fetchNextXWeeks()
			.then(() => this.fetchNextXWeeks()); // To be one step ahead
	};

	getWeek = async (date, pracId) => {
		const { location, treatment } = this.props.selected;
		const nextWeekDate = new Date(date.getTime());
		nextWeekDate.setDate(nextWeekDate.getDate() + 6);
		return Api.getAvailableTimes(location.id, pracId, treatment.id, date, nextWeekDate);
	};

	mergeCalArray = (calArray, id) => {
		const { availableTimes } = this.state;

		if (typeof availableTimes[id] !== 'object') {
			availableTimes[id] = {};
		}

		return calArray.reduce((acc, months) => {
			const monthsArray = Object.keys(months);
			monthsArray.forEach(month => {
				if (typeof acc[month] !== 'object') {
					acc[month] = {};
				}
				const days = months[month];
				const daysArray = Object.keys(days);
				daysArray.forEach(day => {
					if (typeof acc[month][day] !== 'object') {
						acc[month][day] = [];
					}
					const times = days[day];
					acc[month][day] = [...acc[month][day], ...times];
				});
			});
			return acc;
		}, availableTimes[id]);
	};

	today = (year, month, dayOfMonth) => this.present.getDate() === dayOfMonth
		&& this.present.getMonth() === month
		&& this.present.getFullYear() === year;

	past = (year, month, dayOfMonth) => moment(this.present).isAfter(new Date(year, month, dayOfMonth + 1));

	noDates = (year, month, dayOfMonth) => {
		const { practitioners } = this.props.selected;
		return !practitioners.some(prac => this.ensureDatesExist(prac, this.state.availableTimes, month, dayOfMonth));
	};

	ensureDatesExist = (prac, availableTimes, month, dayOfMonth) => availableTimes.hasOwnProperty(prac.id)
		&& availableTimes[prac.id].hasOwnProperty(month)
		&& availableTimes[prac.id][month].hasOwnProperty(dayOfMonth);

	decreaseMonth = () => {
		let year = this.state.year;
		let month = this.state.month - 1;
		if (month < 0) {
			year = year - 1;
			month = 11;
		}
		if (this.past(year, month, this.present.getDate())) {
			return;
		}

		this.setState({ year, month });
	};

	increaseMonth = () => {
		let year = this.state.year;
		let month = this.state.month + 1;
		if (month > 11) {
			year = year +1;
			month = 0;
		}

		const monthDiff = moment(new Date(year, month, this.present.getDate())).diff(moment(this.present), 'months', true);
		if (monthDiff > this.monthAdjust) {
			this.fetchNextXWeeks();
			this.monthAdjust = monthDiff;
		}

		this.setState({ year, month });
	};

	selectTime = (dateTime, prac) => {
		this.props.updateSelections({ slot: dateTime });
		this.props.selectPracFromMultiple(prac);
	};

	constructor(props) {
		super(props);
		this.dayHeaders = [
			'M',
			'T',
			'W',
			'T',
			'F',
			'S',
			'S',
		];

		this.present = new Date();
		this.fetchMoment = moment(this.present.getTime());
		this.monthAdjust = 0;

		this.state = {
			availableTimes: {},
			activeDay: 0,
			activeMonth: -1,
			year: this.present.getFullYear(),
			month: this.present.getMonth(),
			offset: this.calculateStartingOffset(this.present.getFullYear(), this.present.getMonth()),
		};
	}

	componentWillMount() {
		this.getDates();
	}

	render(props, state, context) {
		const { selected } = props;
		const { year, month, offset, activeDay, activeMonth, availableTimes } = state;

		return (

			<div className="info-container" style={{ maxHeight: '95%', marginTop: 0 }}>

				<div className="calendar-date-picker">
					<div onClick={this.decreaseMonth} className="calendar-date-button">
						<img src="/assets/img/arrow.svg" alt="" className="arrow inverse" />
					</div>
					<h4 className="heading-7">{monthNumberToName(month)} {year}</h4>
					<div onClick={this.increaseMonth} className="calendar-date-button">
						<img src="/assets/img/arrow.svg" alt="" className="arrow" />
					</div>
				</div>
				<div data-w-id="eb1d7a6b-abb3-0201-786f-49bbdc0856a8" className="w-layout-grid calendar-grid">
					{this.dayHeaders.map(weekDay => <h4 className="calendar-heading">{weekDay}</h4>)}
					{this.daysInMonth(year, month).map(d => d + 1).map(dayOfMonth => {
						const style = dayOfMonth === 1 ? `grid-column-start: ${offset};` : '';
						const pastOrNoDates = this.past(year, month, dayOfMonth) || this.noDates(year, month, dayOfMonth);
						const classes = [
							'calendar-date-container',
							this.today(year, month, dayOfMonth) ? 'today' : '',
							pastOrNoDates ? 'not-available' : '',
							month === activeMonth && dayOfMonth === activeDay ? 'active' : '',
						].join(' ');

						return (
							<div style={style} className={classes} onClick={() => !pastOrNoDates && this.setState({ activeDay: dayOfMonth, activeMonth: month }) && document.getElementById('times-container').scrollIntoView()}>
								<h4 className="heading-4">{dayOfMonth}</h4>
							</div>
						);
					})}
				</div>
				<div id="times-container" className="times-container">
					{activeDay !== 0 && selected.practitioners
						.filter(prac => this.ensureDatesExist(prac, availableTimes, month, activeDay))
						.map(prac => (
							<div>
								<h4>{selected.practitioners.length > 1 && prac.label}</h4>
								<div className="w-layout-grid times-grid">
									{
										availableTimes[prac.id][month][activeDay].map(dateTime => {
											const active = dateTime === selected.slot ? 'active' : '';
											return (
												<div className={`calendar-time-container ${active}`} onClick={() => this.selectTime(dateTime, prac)}>
													<h4 className="heading-4">{formatTime(dateTime)}</h4>
												</div>
											);
										})
									}
								</div>
							</div>
						))
					}
				</div>
			</div>
		);
	}
}



