import { h, Component } from 'preact';
import Api from '../../api';

export default class Details extends Component {

	handleChange = (e, name) => {
		const str = e.target.value;
		this.props.updateDetails({ [name]: str });
		this.validate();
	};

	validate = () => {
		const { firstName, lastName, email, phoneNumber } = this.props.details;

		const checks = [
			!(/\d/.test(firstName.trim())),
			!(/\d/.test(lastName.trim())),
			/^[^@\s]+@[^@\s]+\.[^@\s]+$/.test(email.trim()),
			/^[+]?[(]?[0-9]{3}[)]?[-\s.]?[0-9]{3}[-\s.]?[0-9]{4,6}$/.test(phoneNumber.trim()),
		];

		this.setState({ valid: checks.every(check => check === true) });
	};

	submit = async () => {
		const { firstName, lastName, email, phoneNumber } = this.props.details;
		const patient = await Api.postNewPatient(firstName, lastName, email, phoneNumber);
		this.props.updateDetails({ patient });
		this.props.page.next();
	};

	constructor(props) {
		super(props);
		this.state = {
			valid: false,
		};
	}

	componentDidMount() {
		this.validate()
	}

	render(props, state, context) {
		return (
			<div data-w-id="06e2e960-7a89-bb5a-9377-e0ee8571adb9" className="booking-card details-screen">
				<div data-w-id="06e2e960-7a89-bb5a-9377-e0ee8571adba" className="panel left full-width">
					<div className="details-form-container w-form">
						<div className="details-form-grid">
							<div className="form-input-container">
								<label htmlFor="name">First Name</label>
								<input type="text" className="text-field w-input" maxLength="256" placeholder="First Name" onKeyUp={e => this.handleChange(e, 'firstName')} onChange={e => this.handleChange(e, 'firstName')} value={props.details.firstName} />
							</div>
							<div className="form-input-container">
								<label htmlFor="email">Last Name</label>
								<input type="text" className="text-field w-input" maxLength="256" placeholder="Last Name" onKeyUp={e => this.handleChange(e, 'lastName')} onChange={e => this.handleChange(e, 'lastName')} value={props.details.lastName} />
							</div>
							<div className="form-input-container">
								<label htmlFor="email">Email</label>
								<input type="text" className="text-field w-input" maxLength="256" placeholder="Email" onKeyUp={e => this.handleChange(e, 'email')} onChange={e => this.handleChange(e, 'email')} value={props.details.email} />
							</div>
							<div className="form-input-container">
								<label htmlFor="email">Phone Number</label>
								<input type="text" className="text-field w-input" maxLength="256" placeholder="Phone Number" onKeyUp={e => this.handleChange(e, 'phoneNumber')} onChange={e => this.handleChange(e, 'phoneNumber')} value={props.details.phoneNumber} />
							</div>
							<div style={{ gridColumnEnd: 'span 2' }} className="form-input-container">
								<label htmlFor="email">Additional Message</label>
								<textarea className="textarea w-input" placeholder="Anything you would like the practitioner to know before arriving. If you have a groupon code please paste it in here." maxLength="5000" onKeyDown={e => this.handleChange(e, 'message')} onChange={e => this.handleChange(e, 'message')} value={props.details.message} />
							</div>
							<input type="submit" value="Go Back" className="submit-button w-button" onClick={props.page.back} />
							<input type="submit" disabled={!state.valid} value="Submit" className="submit-button w-button" onClick={this.submit} />
						</div>
					</div>
					<div className="panel closed" />
				</div>
			</div>
		);
	}
}



