export default class Api {
	static baseUrl = 'https://visitwestwardho.com/img-spc-proxy';

	static treatments = null;
	static businesses = null;

	static debug = true;

	static async getTreatments() {
		if (!Api.treatments) {
			Api.treatments = Api.apiGet('appointment_types')
				.then(data => new Map(data.appointment_types.map(treatment => [`${treatment.id}`, treatment])));
		}

		return Api.treatments;
	}

	static async getLocations() {
		if (!Api.businesses) {
			Api.businesses = Api.apiGet('businesses')
				.then(data => new Map(data.businesses.map(bidnis => [`${bidnis.id}`, bidnis])));
		}

		return Api.businesses;
	}

	static async getPractitionersForLocation(businessId) {
		return Api.apiGet(`businesses/${businessId}/practitioners`)
			.then(data => new Map(data.practitioners.map(prac => [`${prac.id}`, prac])));
	}

	static async getPractitionersForTreatment(treatmentId) {
		return Api.apiGet(`appointment_types/${treatmentId}/practitioners`)
			.then(data => new Map(data.practitioners.map(prac => [`${prac.id}`, prac])));
	}

	static async getAvailableTimes(businessId, pracId, treatmentId, fromDate, toDate) {
		return Api.apiGet(`businesses/${businessId}/practitioners/${pracId}/appointment_types/${treatmentId}/available_times?from=${Api.formatDate(fromDate)}&to=${Api.formatDate(toDate)}`)
			.then(data =>
				data.available_times.reduce((acc, obj) => {
					const date = new Date(obj.appointment_start);
					const month = date.getMonth();
					const day = date.getDate();
					if (typeof acc[month] !== 'object') {
						acc[month] = {};
					}
					acc[month][day] = [...(acc[month][day] || []), date];
					return acc;
				}, {})
			);
	}

	static async postNewPatient(first_name, last_name, email, phone_number) {
		return Api.apiPost(`patients`, {
			first_name,
			last_name,
			email,
			patient_phone_numbers: [
				{
					number: phone_number,
					phone_type: 'Mobile',
				},
			],
		});
	}

	static async postNewAppointment(starts_at, patient_id, practitioner_id, appointment_type_id, business_id) {
		return Api.apiPost(`individual_appointments`, {
			starts_at: starts_at.toISOString(),
			patient_id,
			practitioner_id,
			appointment_type_id,
			business_id,
		});
	}

	static async apiGet(url) {
		return fetch(`${Api.baseUrl}/${url}`)
			.then(Api.status)
			.then(Api.json)
			.then((data) => {
				// console.log('Request succeeded with JSON response: `', data, '`');
				return data;
			})
			.catch((error) => {
				console.log('Request failed: `', error, '`');
				if (Api.debug) {
					console.log('Retrying local file...');
					return fetch(`/assets/fixtures/${url}.json`)
						.then(Api.json)
						.catch(error => {
							console.log('Local request failed: `', error, '`');
						});
				}
			});
	}

	static async apiPost(url, data) {
		return fetch(`${Api.baseUrl}/${url}`, {
			method: 'POST',
			body: JSON.stringify(data)
		})
			.then(Api.status)
			.then(Api.json)
			.then((data) => {
				// console.log('Request succeeded with JSON response: `', data, '`');
				return data;
			})
			.catch((error) => {
				console.log('Request failed: `', error, '`');
			});
	}

	static formatDate(date) {
		return `${date.getFullYear()}-${Api.zeroPad(date.getMonth() + 1)}-${Api.zeroPad(date.getDate())}`;
	}

	static zeroPad(num) {
		return ('0' + num).slice(-2);
	}

	static status(response) {
		if (response.status >= 200 && response.status < 300) {
			return Promise.resolve(response);
		}

		console.log(response);
		return Promise.reject(new Error(response.statusText));
	}

	static json(response) {
		return response.json();
	}

}



