import { h, Component } from 'preact';
import ReactPixel from 'react-facebook-pixel';
// import '../webflow';

import TreatmentLocation from './treatmentlocation';
import Practitioner from './practitioner';
import DateAndTime from './dateandtime';
import Details from './details';
import Confimation from './confirmation';
import Thanks from './thanks';

export default class App extends Component {

	static selectedReset = {
		treatment: null,
		location: null,
		practitioners: [],
		slot: null,
	};

	back = () => {
		this.setState({ pageNum: Math.max(this.state.pageNum - 1, 0) });
	};

	nextPage = () => {
		let page = this.state.pageNum + 1;
		if (page >= this.pages.length) {
			page = 0;
		}
		this.setState({ pageNum: page });
	};

	updateSelections = updatedProperties => {
		// if (updatedProperties.practitioners && this.state.selected.practitioners &&
		// 		updatedProperties.practitioners[0] && this.state.selected.practitioners[0] &&
		// 		updatedProperties.practitioners[0].id !== this.state.selected.practitioners[0].id) {
		// 	updatedProperties.slot = null;
		// }

		this.setState({ selected: { ...this.state.selected, ...updatedProperties } });
	};

	updateDetails = updatedProperties => {
		this.setState({ details: { ...this.state.details, ...updatedProperties } });
	};

	constructor(props) {
		super(props);
		this.state = {
			pageNum: 0,
			selected: App.selectedReset,
			details: {
				firstName: '',
				lastName: '',
				phoneNumber: '',
				email: '',
				message: '',
				patient: null,
			},
		};
	}

	componentWillMount() {
		this.setState({ pageNum: 0 });
		const page = {
			next: this.nextPage,
			back: this.back,
		};
		this.pages = [
			() => <TreatmentLocation selected={this.state.selected} page={page} updateSelections={this.updateSelections} />,
			() => <Practitioner selected={this.state.selected} page={page} updateSelections={this.updateSelections} />,
			() => <DateAndTime selected={this.state.selected} page={page} updateSelections={this.updateSelections} />,
			() => <Details selected={this.state.selected} details={this.state.details} page={page} updateSelections={this.updateSelections} updateDetails={this.updateDetails} />,
			() => <Confimation selected={this.state.selected} details={this.state.details} page={page} updateSelections={this.updateSelections} />,
			() => <Thanks page={page} />,
		];
	}

	componentDidMount() {
		const advancedMatching = {};
		const options = {
			autoConfig: true,
			debug: false,
		};
		ReactPixel.init('2218233931784565', advancedMatching, options);
		ReactPixel.pageView();
	}

	render(props, state, context) {
		return (
			<div>
				<div className="app-container">
					{this.pages[state.pageNum]()}
				</div>
			</div>
		);
	}
}



