import { h, Component } from 'preact';
import thanksImg from '../../assets/img/undraw_reading_0re1.svg';

export default class Thanks extends Component {
	render(props, state, context) {
		return (
			<div className="booking-card final-screen">
				<div className="panel left full-width final-screen">
					<img src={thanksImg} width="304" alt="" />
					<h3 className="app-heading">Thanks for booking!</h3>
					<div className="w-form">
						<input type="submit" value="Book again" data-wait="Please wait..." className="submit-button w-button" onClick={props.page.next} />
					</div>
				</div>
				<div className="panel closed" />
			</div>
		);
	}
}



