import Api from '../api';

const monthNumberNameMap = {
	0: 'January',
	1: 'February',
	2: 'March',
	3: 'April',
	4: 'May',
	5: 'June',
	6: 'July',
	7: 'August',
	8: 'September',
	9: 'October',
	10: 'November',
	11: 'December',
};

export const monthNumberToName = (number) => monthNumberNameMap[number] || '-';

export const formatTime = (dateTime) => {
	let hours = dateTime.getHours();
	let meridian = 'am';

	if (hours === 12) {
		meridian = 'pm';
	}
	else if (hours > 12) {
		hours -= 12;
		meridian = 'pm';
	}
	return `${hours}:${Api.zeroPad(dateTime.getMinutes())}${meridian}`;
};

export const formatDateAndTime = slot => `${slot.getDate()} ${monthNumberToName(slot.getMonth())} ${slot.getFullYear()} at ${formatTime(slot)}`;




