import { h, Component } from 'preact';

export default class PractitionerPortrait extends Component {

	static getImgName(name) {
		const nameSlug = name.toLowerCase().replace(' ', '_').replace(/\W/, '');
		return `/assets/img/practitioners/${nameSlug}.png`;
	}

	render(props, state, context) {
		const { practitioner, selected } = props;
		const active = selected && selected.id === practitioner.id ? 'active' : '';
		return (
			<div className="practitioner-info-container">
				<div className={`prac-portrait ${active}`} onClick={() => props.onClick(`${practitioner.id}`)}>
					<img src={PractitionerPortrait.getImgName(practitioner.label)} alt={practitioner.label} />
				</div>
				<h5 className="pract-name">{practitioner.label}</h5>
				<h6 className="pract-tagline">{practitioner.designation}</h6>
			</div>
		);
	}
}



